		height: 16rem;
	}
}
.container {
	display: flex;
	flex-direction: column;
}

.flextext {
  display: flex;
  align-content: center;
  align-items: center;
  justify-content: center;
}

.initial-image {
  width: 15rem;
  height: 15rem;
}

.signin {
  margin-top: 15rem;
  background-color: white;
}

